.app {
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.bkg {
  width: 100vw;
  height: 100vh;
  backdrop-filter: blur(16px);
  background-color: #77777770;
}

.container {
  width: 70vw;
  height: 70vh;
  margin: 15vh 15vw;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  border-radius: 1.5rem;
}

.app,
.container {
  background-image: url("../assets/bkg2.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
}

.err{
  display: flex;
  font-size: 4rem;
  align-self:center;
  text-align: center;
}

@media only screen and (max-width: 400px){

  .container {
    height: 92vh;
    margin: 2vh 15vw;
  }
}